function InfoCard(props) {
    const {title, text, image } = props;
    return (
      <div className={`bg-blue-600 p-2 lg:p-3 rounded-3xl mb-5 place-items-center`}>
        <div
          className={`bg-yellow-400 text-center p-2 sm:p-3 lg:p-5 rounded-2xl  mx-auto text `}
        >
          <div className="flex justify-center p-2">
            <div className={`bg-yellow-100 p-1 rounded-2xl`}>
              <h1
                className={`bg-blue-300 sm:text-2xl text-bold rounded-2xl  px-3 sm:px-10 lg:px-24  p-1  md:px-16 font font-bold text-sm`}
              >
                {title}
              </h1>
            </div>
          </div>
          <div className="flex justify-evenly">
            <div className="flex place-items-center">
            <h3
              className={`bg-yellow-100 sm:text-xl text-xs text-bold rounded-3xl lg:px-10 p-1 sm:p-3 px-3 font-light text-justify`}
            >
              {text}
            </h3>
            </div>
            {image ? (<img className={`bg-yellow-100 p-0.5 rounded-full w-8 h-8 sm:w-16 sm:h-16` } src={image} alt=""></img>):(<></>)}
          </div>
        </div>
      </div>
    );
  }
export default InfoCard;  